<template>
  <div
    class="v-select"
    tabindex="1"
    :class="{ active: selectVisible }"
    @blur="selectVisible = false"
  >
    <div class="v-select__wrapp">
      <div class="v-select__value" @click="visbleSelect">
        {{ value }}
      </div>
    </div>

    <transition name="fade">
      <div class="v-select__list" v-show="selectVisible">
        <div
          class="v-select__item"
          v-for="option in options"
          :key="option.id"
          :data-id="option.id"
          @click="setValue"
        >
          {{ option.text }}
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: "v-select",
  props: {
    options: Array,
  },
  data() {
    return {
      value: `${this.options[0].text}`,
      selectVisible: false,
    };
  },
  methods: {
    visbleSelect() {
      this.selectVisible = this.selectVisible ? false : true;
    },
    setValue(e) {
      const id = e.target.getAttribute("data-id");
      this.value = this.options[id].text;
      this.selectVisible = false;
    },
  },
  watch: {
    value() {
      this.$emit("input", this.value);
    },
  },
};
</script>

<style lang="scss">
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.1s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active до версии 2.1.8 */ {
  opacity: 0;
}

.v-select {
  max-height: 100%;
  word-break: break-word;
  display: flex;
  flex-direction: column;
  font-size: 14px;
  position: relative;
  cursor: pointer;
  background: #f6f8f9;
  user-select: none;
  color: rgba(0, 0, 0, 0.7);

  &:after {
    content: "";
    display: block;
    position: absolute;
    right: 8px;
    width: 20px;
    height: 10px;
    top: 50%;
    transform: translateY(-50%);
    background-image: url("~@/assets/images/select-arr.svg");
    background-color: #f6f8f9;
    background-repeat: no-repeat, repeat;
    background-size: contain;
    transition: 0.1s;

    @media (max-width:567px) {
      width: 13px;
      height: 8px;
    }
  }

  &__list {
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 99;

    display: flex;
    flex-direction: column;

    background-color: #f6f8f9;
    box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.25);
    width: 100%;
  }
  &__item {
    cursor: pointer;
    max-width: 100%;
    overflow: hidden;
    padding: 10px 20px;
    position: relative;

    @media (max-width: 567px) {
      padding: 8px 10px;
    }
    @media (max-width: 380px) {
      padding: 5px 8px;
    }
  }
  &__item:hover {
    background: #0087d4;
    color: #fff;
  }
  font-size: 14px;
  font-family: "Inter";
  padding: 10px 20px;

  border: 1px solid #c4c4c4;

  // background-color: #F6F8F9;

  @media (max-width: 568px) {
    padding: 7px 3px 8px 8px;
    font-size: 12px;
  }
  @media (max-width:380px) {
    padding: 7px 3px 8px 5px;
    font-size: 10px;
  }
}
.v-select__value {
  white-space: nowrap;
  text-overflow: ellipsis;
  position: relative;
  max-width: 100%;
  max-height: 100%;
}
.v-select.active {
  &:after {
    transform: rotate(-180deg);
    top: 39%;
  }
}
.v-select:hover {
  border-color: #888;
}
.v-select:focus {
  border-color: #aaa;
  box-shadow: 0 0 1px 3px rgba(59, 153, 252, 0.7);
  box-shadow: 0 0 0 3px -moz-mac-focusring;
  color: #222;
  outline: none;
}
</style>